.wrapper {
  display: flex;
  flex-direction: column;
  position: absolute;
  bottom: 0;
  left: 0;
  background-color: rgba($color: #0d0f248a, $alpha: 0.35);
  width: 26em;
  max-height: 16em;
  .PlayerChat {
    overflow: auto;
    scroll-behavior: smooth;
    max-height: 16em;
    &::-webkit-scrollbar {
      width: 10px;
    }
    &::-webkit-scrollbar-thumb {
      background-color: rgba(30, 38, 47, 0.5);
    }
    .user {
      color: lime;
    }
    .rival {
      color: wheat;
    }
  }
  .chat_footer {
    display: flex;
    .chat_box {
      box-sizing: border-box;
      background-color: rgba(13, 15, 36, 0.5);
      width: 100%;
      border: none;
      color: wheat;
      border-top: solid 1px black;
      padding: 0.6em 0 0.6em 0.6em;
      resize: none;
      &:focus {
        outline: none;
      }
    }
    .sendButton {
      background-color: rgb(20 22 46 / 74%);
      color: wheat;
      font-weight: 600;
      border: none;
      &:active {
        background-color: rgba(20, 22, 46, 0.877);
      }
    }
  }
}
